.profile_link {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  grid-gap: 20px;
}

.profile_menu {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.profile_menu .profile_menu__logo_img {
  width: 42.5px;
  height: 35px;
  background-color: #fff;
  border-radius: 100px;
  overflow: hidden;
  margin-right: 10px;
}

.profile_menu .profile_menu__logo_img img {
  height: 100%;
  border-radius: 100%;
}
