.footer {
  background-color: #E0E0E0;
}

.footer div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.footer .footer__contact {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}

.footer .footer__contact p {
  display: inline-block;
}

.footer .footer__contact ul {
  display: inline-block;
}

.footer .footer__contact ul li {
  display: inline-block;
}

.footer .footer__response {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  grid-gap: 30px;
}

.footer .footer__response div {
  padding: 10px 20px;
  border-radius: 10px;
  background-color: #A30CFF;
}
