.news_slider {
    display: flex;
    justify-content: space-between;
    background-color: #222;
    border-radius: 15px;
    padding: 30px;
    color: #fff;
    position: relative;

    .news_slider__text{
        width: 100%;
        padding: 0 20px;
        .news_slider__title {
            font-size: 16px*2;
            margin-bottom: 20px;
        }
    }

    .news_slider__past {
        display: flex;
        align-items: center;
        transform: rotateZ(-90deg);
        font-size: 22px;
    }
    
    .news_slider__next {
        display: flex;
        align-items: center;
        transform: rotateZ(90deg);
        font-size: 22px;
    }
}