.profile_link {
    display: flex;
    align-items: center;
    grid-gap: 20px;
}

.profile_menu {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .profile_menu__logo_img {
        width: 42.5px;
        height: 35px;
        background-color: #fff;
        border-radius: 100px;
        overflow: hidden;
        
        
        img {
            height: 100%;
            border-radius: 100%;
        }

        margin-right: 10px;
    }
}