.category{
    ul{
        display: grid;
        grid-gap: 4px;
        li{
            display: flex;
            align-items: center;
            justify-content: flex-start;
            grid-gap: 20px;
            padding: 10px 20px;
            &:first-child{
                border: 2px solid #BD00FF;
                border-radius: 30px;
            }
        }
    }
    div{
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #BD00FF;
        transform: rotateZ(180deg);
        padding: 3px 0;
        font-weight: 900;
        border-radius: 10px 10px 0 0;
    }
}