.footer {
    div{
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
    background-color: #E0E0E0;

    .footer__contact {
        display: flex;
        p{
            display: inline-block;
            
        }
        ul {
            display: inline-block;
            li {
                display: inline-block;
            }
        }

    }
    .footer__response{
        display: flex;
        grid-gap: 30px;
        div{
            padding: 10px 20px;
            border-radius: 10px;
            background-color: #A30CFF;
        }
    }
}