.popular_request{
    margin-top: 80px;
    .request_list{
        display: flex;
        align-items: center;
        grid-gap: 20px;
        p{
            font-weight: bolder;
        }
        li{
            display: inline-block;
            padding: 5px 25px;
            border-radius: 30px;
            box-shadow: 0 4px 5px #aaa;
            color: #0029FF;
        }
    }

}