.popular_request {
  margin-top: 80px;
}

.popular_request .request_list {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  grid-gap: 20px;
}

.popular_request .request_list p {
  font-weight: bolder;
}

.popular_request .request_list li {
  display: inline-block;
  padding: 5px 25px;
  border-radius: 30px;
  -webkit-box-shadow: 0 4px 5px #aaa;
          box-shadow: 0 4px 5px #aaa;
  color: #0029FF;
}
