.category ul {
  display: -ms-grid;
  display: grid;
  grid-gap: 4px;
}

.category ul li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
  grid-gap: 20px;
  padding: 10px 20px;
}

.category ul li:first-child {
  border: 2px solid #BD00FF;
  border-radius: 30px;
}

.category div {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  background-color: #BD00FF;
  -webkit-transform: rotateZ(180deg);
          transform: rotateZ(180deg);
  padding: 3px 0;
  font-weight: 900;
  border-radius: 10px 10px 0 0;
}
