.latest_update {

    .latest_update__title_box {
        display: flex;
        justify-content: flex-start;
        align-items: center;
        grid-gap: 100px;
        margin-bottom: 30px;

        .latest_update__title {
            font-size: 32px;
        }

        .latest_update__location {
            display: flex;
            justify-content: center;
            align-items: center;
            grid-gap: 5px;
            padding: 5px 15px;
            border-radius: 20px;
            background-color: #E2E2E2;
        }
    }

    .latest_update__advert_list {
        display: grid;
        grid-template-columns: repeat(5, 1fr);
        grid-gap: 30px;

        .latest_update__advert {
            display: grid;
            grid-template-rows: 1.5fr 1fr;

            .latest_update__advert_img {
                background-color: #000;
                border-radius: 20px;
            }

            .latest_update__advert_info {
                padding: 10px 0;
                .latest_update__advert_some_section {
                    display: flex;
                    justify-content: flex-start;
                    grid-gap: 30px;

                    .latest_update__advert_price {}

                    .latest_update__advert_option {
                        display: flex;
                        grid-gap: 15px;
                        li {
                            display: inline-block;
                        }
                    }
                }

                .latest_update__advert_set {
                    color: #0029FF;
                    li {
                        display: inline-block;
                    }

                    .latest_update__advert_set_divide {
                        margin: 0 10px;
                    }
                }

                .latest_update__advert_text {
                    overflow: hidden;

                    p {
                        display: -webkit-box;
                        -webkit-line-clamp: 3;
                        -webkit-box-orient: vertical;
                    }
                }
            }
        }
    }
}