.popular_shop__title {
  font-size: 32px;
  margin-bottom: 30px;
}

.popular_shop__shop_card {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr 1fr 1fr 1fr;
      grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
  grid-gap: 60px;
}

.popular_shop__shop_card li {
  display: -ms-grid;
  display: grid;
  -ms-grid-rows: 1.5fr 1fr;
      grid-template-rows: 1.5fr 1fr;
}

.popular_shop__shop_card li .popular_shop__preview {
  position: relative;
}

.popular_shop__shop_card li .popular_shop__preview .popular_shop__img {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  background-color: #000;
}

.popular_shop__shop_card li .popular_shop__preview .popular_shop__logo {
  position: absolute;
  top: 50%;
  left: 0;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  width: 60px;
  height: 60px;
  border-radius: 30px;
  background-color: #f00;
}

.popular_shop__shop_card li .popular_shop__info {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 1fr;
      grid-template-columns: 1fr 1fr;
  -ms-grid-rows: 1fr 1fr 1fr;
      grid-template-rows: 1fr 1fr 1fr;
  grid-gap: 10px;
  font-size: 14px;
}

.popular_shop__shop_card li .popular_shop__info .popular_shop__location {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  grid-gap: 10px;
}

.popular_shop__shop_card li .popular_shop__info .popular_shop__number li {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  grid-gap: 10px;
}
