.popular_shop__title{
    font-size: 32px;
    margin-bottom: 30px;
}

.popular_shop__shop_card{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr 1fr;
    grid-gap: 60px;
    li{
        display: grid;
        grid-template-rows: 1.5fr 1fr;
        .popular_shop__preview{
            position: relative;
            .popular_shop__img{
                width: 100%;
                height: 100%;
                border-radius: 20px;
                background-color: #000;
            }
            .popular_shop__logo{
                position: absolute;
                top: 50%;
                left: 0;
                transform: translate(-50% , -50%);
                width: 60px;
                height: 60px;
                border-radius: 30px;
                background-color: #f00;
            }
        }
        .popular_shop__info{
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 1fr 1fr 1fr;
            grid-gap: 10px;
            font-size: 14px;
            .popular_shop__name{

            }
            .popular_shop__rank{

            }
            .popular_shop__location{
                display: flex;
                align-items: center;
                grid-gap: 10px;

            }
            .popular_shop__work_time{

            }
            .popular_shop__number{
                li{
                    display: flex;
                    align-items: center;
                    grid-gap: 10px;
                }
            }
            .popular_shop__social_link{

            }
        }
    }
}