.switch_location {
  position: relative;
  width: 111.88px;
  -webkit-transition: 1s;
  transition: 1s;
  background-color: #E2E2E2;
  padding: 5px 25px;
  border-radius: 5px;
}

.switch_location:hover {
  width: 141.88px;
  border-radius: 5px 5px 0 0;
}

.switch_location:hover .switch_location__deploy_box {
  height: 156.8px;
  border-radius: 0 0 5px 5px;
}

.switch_location .switch_location__title {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  grid-gap: 10px;
}

.switch_location .switch_location__deploy_box {
  position: absolute;
  overflow: hidden;
  height: 0;
  width: 100%;
  -webkit-transition: 1s;
  transition: 1s;
  left: 0;
  top: 100%;
}

.switch_location .switch_location__location_list {
  position: absolute;
  width: 100%;
  bottom: 0;
  height: 156.8px;
  background-color: #E2E2E2;
  overflow: scroll;
  padding: 10px 5px;
}

.switch_location .switch_location__location_list::-webkit-scrollbar {
  width: 0;
  height: 0;
}
