.news_slider {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background-color: #222;
  border-radius: 15px;
  padding: 30px;
  color: #fff;
  position: relative;
}

.news_slider .news_slider__text {
  width: 100%;
  padding: 0 20px;
}

.news_slider .news_slider__text .news_slider__title {
  font-size: 32px;
  margin-bottom: 20px;
}

.news_slider .news_slider__past {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-transform: rotateZ(-90deg);
          transform: rotateZ(-90deg);
  font-size: 22px;
}

.news_slider .news_slider__next {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-transform: rotateZ(90deg);
          transform: rotateZ(90deg);
  font-size: 22px;
}
