.search_advert {
  padding: 0 40px;
}

.search_advert input {
  width: 500px;
  height: 25px;
  padding: 0 10px;
  border-radius: 20px;
  border: 1px #ABABAB solid;
}

.search_advert input::-webkit-input-placeholder {
  text-align: center;
}

.search_advert input:-ms-input-placeholder {
  text-align: center;
}

.search_advert input::-ms-input-placeholder {
  text-align: center;
}

.search_advert input::placeholder {
  text-align: center;
}
