@import './../../asset/scssSiteSet/verbale.scss';

$townsOnce: 7;

.switch_location {
    position: relative;
    width: 91.88px+20px;
    transition: 1s;
    background-color: #E2E2E2;
    padding: 5px 25px;
    border-radius: 5px;

    &:hover {
        width: 91.88px+20px+30px;
        border-radius: 5px 5px 0 0 ;
        .switch_location__deploy_box {
            height: $townsOnce*$font-size*$line-height;
            border-radius:  0 0 5px 5px;
        }
    }

    .switch_location__title {
        display: flex;
        justify-content: center;
        align-items: center;
        grid-gap: 10px;
    }

    .switch_location__deploy_box {
        position: absolute;
        overflow: hidden;
        height: 0;
        width: 100%;
        transition: 1s;
        left: 0;
        top: 100%;
    }

    .switch_location__location_list {
        position: absolute;
        width: 100%;
        bottom: 0;
        height: $townsOnce*$font-size*$line-height;

        background-color: #E2E2E2;

        overflow: scroll;

        padding: 10px 5px;

        &::-webkit-scrollbar {
            width: 0;
            height: 0;
        }
    }

}